/* HeroSection.css */

.hero-section {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 2rem;
    background: #f4f4f4;
  }
  
  .hero-content {
    display: flex;
    width: 100%;
  }
  
  .hero-text {
    width: 50%;
    padding: 2rem;
  }
  
  .hero-image {
    width: 50%;
    height: auto;
    border-radius: 8px;
  }
  
  .booking-form {
    position: absolute;
    top: 90%;
    left: 50%;
    transform: translate(-50%, -50%);
    display: flex;
    align-items: center;
    gap: 1rem;
    background: rgba(255, 255, 255, 0.9);
    padding: 1rem 1.5rem;
    border-radius: 8px;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
  }
  
  .form-field {
    display: flex;
    flex-direction: column;
  }
  
  .form-field label {
    font-size: 12px;
    color: #333;
    margin-bottom: 0.25rem;
  }
  
  .book-button {
    background-color: #007bff;
    color: white;
    border: none;
    padding: 0.5rem 1.25rem;
    cursor: pointer;
    border-radius: 4px;
    font-weight: bold;
  }
  
  /* Adjust for smaller screens */
  @media (max-width: 768px) {
    .hero-content {
      flex-direction: column;
    }
  
    .hero-text,
    .hero-image {
      width: 100%;
    }
  
    .booking-form {
      position: relative;
      top: auto;
      left: auto;
      transform: none;
      margin-top: 1rem;
      flex-direction: column;
    }
  }
  