.confirmation {
    font-family: Arial, sans-serif;
    max-width: 800px;
    margin: 0 auto;
    padding: 20px;
    color: #333;
  }
  
  .confirmation-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-bottom: 20px;
    border-bottom: 1px solid #ddd;
  }
  
  .confirmation-header h1 {
    font-size: 1.8rem;
    font-weight: bold;
  }
  
  .confirmation-nav a {
    margin: 0 15px;
    color: #555;
    text-decoration: none;
    font-weight: 500;
  }
  
  .confirmation-nav a:hover {
    color: #000;
  }
  
  .booking-info {
    background: #f9f9f9;
    padding: 20px;
    border-radius: 8px;
    display: flex;
    justify-content: space-between;
    margin: 20px 0;
  }
  
  .personal-info input {
    display: block;
    width: 100%;
    padding: 10px;
    margin: 10px 0;
    border: 1px solid #ccc;
    border-radius: 5px;
    font-size: 1rem;
    color: #666;
  }
  
  .date-room-info {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  
  .date-room-info div {
    margin-bottom: 15px;
  }
  
  .date-room-info label {
    font-size: 0.9rem;
    color: #888;
    margin-bottom: 5px;
  }
  
  .date-room-info p {
    font-size: 1.1rem;
    color: #333;
  }
  
  .price h2 {
    color: #007bff;
    font-size: 1.6rem;
    margin-bottom: 10px;
  }
  
  .pay-button {
    background-color: #007bff;
    color: #fff;
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 1rem;
  }
  
  .pay-button:hover {
    background-color: #0056b3;
  }
  
  .confirmation-footer {
    display: flex;
    justify-content: space-between;
    padding-top: 20px;
    border-top: 1px solid #ddd;
  }
  
  .footer-left h2 {
    font-size: 1.2rem;
    margin-bottom: 10px;
  }
  
  .footer-left address {
    font-style: normal;
    line-height: 1.5;
  }
  
  .location-button {
    background-color: #007bff;
    color: #fff;
    padding: 8px 16px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    margin-top: 10px;
  }
  
  .location-button:hover {
    background-color: #0056b3;
  }
  
  .footer-right a {
    display: block;
    color: #555;
    text-decoration: none;
    margin-bottom: 5px;
  }
  
  .footer-right a:hover {
    color: #007bff;
  }
  