/* ServicesSection.css */
.services-section {
    padding: 2rem;
    background: #f4f4f4;
  }
  
  .services-grid {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }
  
  .service-card {
    width: 200px;
    margin: 1rem;
    padding: 1rem;
    background: #fff;
    border-radius: 8px;
    box-shadow: 0px 0px 5px rgba(0,0,0,0.1);
  }
  